	export default {
    components: {
    },
		data() {
			return {
        isCar: ['是', '否'],
        isViolation: ['是', '否'],
        oilType: ['汽油', '柴油', '机油', '液化气'],
        isInvoice: ['是', '否'],
        isQualified: ['是', '否'],
        isApply: ['是', '否'],
        isSluggish: ['是', '否'],
        index: 0,
        facadeTempFilePaths: '',
        obverseTempFilePaths: ''
			}
		},
		onLoad() {

		},
		methods: {
      bindPickerChange: function(e) {
        console.log('picker发送选择改变，携带值为', e.target.value)
        this.index = e.target.value
      },
      // 正面拍照
      takePhotoFacade() {
        const _this = this;
        wx.chooseImage({
          count: 1,
          sizeType: ['original', 'compressed'],
          sourceType: ['album', 'camera'],
          success(res) {
            // tempFilePath可以作为img标签的src属性显示图片
            _this.facadeTempFilePaths = res.tempFilePaths[0]
          }
        })
      },
      // 反面照片
      takePhotoObverse() {
        const _this = this;
        wx.chooseImage({
          count: 1,
          sizeType: ['original', 'compressed'],
          sourceType: ['album', 'camera'],
          success(res) {
            // tempFilePath可以作为img标签的src属性显示图片
            _this.obverseTempFilePaths = res.tempFilePaths[0]
          }
        })
      },
      // 点击放大正面图片
      facadeChangeImage() {
        wx.predivImage({
          current: 'tempFilePaths', // 当前显示图片的http链接
          urls: [this.facadeTempFilePaths] // 需要预览的图片http链接列表
        })
      },
      // 点击放大反面图片
      obverseChangeImage() {
        wx.predivImage({
          current: 'tempFilePaths', // 当前显示图片的http链接
          urls: [this.obverseTempFilePaths] // 需要预览的图片http链接列表
        })
      },
      // 提交
      submit() {

      }
      // carInfo() {
      //    wx.navigateTo({
      //     url: '/pages/goods/main'
      //   })
      // },
		}
	}
